.contact-section {
  padding-top: 50px;
  background: #161924 !important;
  color: #fff;
}

.contact-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  background: linear-gradient(
    to bottom,
    #8ff809 -41%,
    rgba(102, 216, 46, 0) 30%
  );
  border-radius: 15px;
  margin-left: 56px;
  margin-right: 56px;
}

.contact-info {
  flex: 1;
  padding: 20px;
}

.contact-info h2 {
  font-family: Orbitron, sans-serif;
  font-size: 2em;
  margin-bottom: 10px;
  text-align: left;
}

.contact-info p {
  text-align: left;
}

.contact-info .highlight {
  background: linear-gradient(90deg, #66d82e 60.81%, #367218 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.contact-info p {
  font-family: Arial, sans-serif;
  margin-bottom: 20px;
}

.form-container {
  flex: 2;
  padding: 20px;
}

.social-links {
  text-align: left;
  margin-left: 80px;
}

.social-links p {
  font-family: "Orbitron", sans-serif !important;
  font-weight: 600;
  font-size: 36px;
  margin-bottom: 4px;
  color: #fff;
}

.social-links .highlight {
  background: linear-gradient(90deg, #66d82e 60.81%, #367218 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.social-links .icons {
  margin-top: 0px;
}

.social-links .icons button {
  margin-top: 0px;
  background: none;
  border: none;
  color: grey;
  font-size: 1.5em;
  cursor: pointer;
}

.social-links .icons button:hover {
  color: #66d82e;
}

.copy-right p {
  padding-bottom: 10px;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 0px !important;
}

copy-right h3 {
  padding-bottom: 5px !important;
}

/* Media Queries */

/* Extra small devices (phones, less than 576px) */
@media (max-width: 575.98px) {
  .contact-container {
    flex-direction: column;
    margin-left: 20px;
    margin-right: 20px;
    padding: 10px;
  }

  .contact-info {
    text-align: center;
    padding: 10px;
  }

  .contact-info h2 {
    font-size: 1.5em;
  }

  .form-container {
    padding: 10px;
  }

  .social-links {
    text-align: center;
    margin-left: 0;
  }

  .social-links p {
    font-size: 24px;
  }

  .social-links .icons button {
    font-size: 1.2em;
  }

  .copy-right p {
    text-align: center;
  }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .contact-container {
    flex-direction: column;
    margin-left: 30px;
    margin-right: 30px;
    padding: 15px;
  }

  .contact-info {
    text-align: center;
    padding: 15px;
  }

  .contact-info h2 {
    font-size: 1.75em;
  }

  .form-container {
    padding: 15px;
  }

  .social-links {
    text-align: center;
    margin-left: 0;
  }

  .social-links p {
    font-size: 28px;
  }

  .social-links .icons button {
    font-size: 1.3em;
  }

  .copy-right p {
    text-align: center;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .contact-container {
    flex-direction: row;
    margin-left: 40px;
    margin-right: 40px;
    padding: 20px;
  }

  .contact-info h2 {
    font-size: 2em;
  }

  .form-container {
    padding: 20px;
  }

  .social-links {
    text-align: left;
    margin-left: 60px;
  }

  .social-links p {
    font-size: 32px;
  }

  .social-links .icons button {
    font-size: 1.4em;
  }

  .copy-right p {
    text-align: center;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .contact-container {
    flex-direction: row;
    margin-left: 50px;
    margin-right: 50px;
    padding: 25px;
  }

  .contact-info h2 {
    font-size: 2.25em;
  }

  .form-container {
    padding: 25px;
  }

  .social-links {
    text-align: left;
    margin-left: 70px;
  }

  .social-links p {
    font-size: 34px;
  }

  .social-links .icons button {
    font-size: 1.5em;
  }

  .copy-right p {
    text-align: left;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .contact-container {
    flex-direction: row;
    margin-left: 56px;
    margin-right: 56px;
    padding: 30px;
  }

  .contact-info h2 {
    font-size: 2.5em;
  }

  .form-container {
    padding: 30px;
  }

  .social-links {
    text-align: left;
    margin-left: 80px;
  }

  .social-links p {
    font-size: 36px;
  }

  .social-links .icons button {
    font-size: 1.5em;
  }

  .copy-right p {
    text-align: left;
  }

  .copy-right h3 {
    font-family: "Orbitron", sans-serif !important;
    padding-bottom: 5px !important;
    /* /* font-size: 15px; */
  }
}
